@import "./src/assets/sass/prepends.scss";

.card {
  padding: 0.5rem;
  margin-bottom: 2rem;

  h4 {
    padding: 0.5rem 0 0.75rem;
  }

  ul li div {
    display: inline-block;

    &.date {
      width: 6rem;
    }
    &.room {
      width: calc(100% - 6rem);
      min-width: 10rem;
      input {
        width: 100%;
      }
    }
  }
}

input::placeholder {
  color: $text-dark;
}
.text-danger {
  color: $text-red;
}
.text-primary {
  color: $text-blue;
}
.text-success {
  color: $text-green;
}
.text-muted {
  color: $text-dark;
}
#api-saving-text {
  display: block;
  position: fixed;
  bottom: 1rem;
  left: 1rem;
}
