@import "./src/assets/sass/prepends.scss";

.card {
  padding: 1rem;
  margin-bottom: 1rem;
  width: 100%;
}
#today-room {
  margin: 1rem auto;
  #today-room-name {
    font-size: 1.2rem;
    line-height: 1.5;
  }
}
h4 {
  display: block;
  margin: 0 0 1rem;
}
#register .button {
  display: inline-block;
  margin: 0;
  padding: 0.75rem;
}
#ical .button {
  display: inline-block;
  margin: 0.5rem 0;
  padding: 0.75rem;
}
.text-danger {
  color: $text-red;
}
.text-primary {
  color: $text-blue;
}
.text-success {
  color: $text-green;
}
.text-muted {
  color: $text-dark;
}
